@charset "utf-8";

@font-face {
  font-family: 'GmarketSansMedium';
  src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansMedium.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}
*{margin: 0; padding: 0;}

body{
  font-family: 'GmarketSansMedium';
}

header{
  width: 100%;
  height: 80px;
  position: fixed;
  top: 0px;
  background-color: white;
}
header h1{
  width: 60px;
  height: 60px;
  margin: 0 auto;
  padding: 10px;
}

header h1 img{
  width: 100%;
}

main{
  margin: 10px;
  margin-top: 180px;
}


footer{
  background-color: #222;
  width: 100%;
  line-height: 40px;
}

footer address{
  color: white;
  text-align: center;
  font-style: normal;
}


/* 내용 */
h2{
  text-align: center;
}
pre{
  padding: 10px;
  background-color: rgb(250, 216, 222);
}

ul{
  margin: 10px 0;
  padding: 10px;
  background-color: rgb(248, 248, 200);
}

li{
  list-style: none;
}



