@charset "utf-8";

.movie{
  text-align: center;
  padding-top: 150px;
}

.MovieList{
  width: 80%;
  margin: 0 auto;
  padding-top: 50px;
  background-color: white;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.MovieList >li{
  width: 230px;
  margin: 10px;
}

.movie_title{
  font-weight: bold;
  font-size: 14px;
}

.movie_sub{
  font-size: 12px;
}